:root {
  --green: #6aaa64;
  --darkendGreen: #538d4e;
  --yellow: #c9b458;
  --darkendYellow: #b59f3b;
  --lightGray: #d8d8d8;
  --gray: #86888a;
  --darkGray: #939598;
  --white: #fff;
  --black: #212121;
  /* Colorblind colors */
  --orange: #f5793a;
  --blue: #85c0f9;
  --partialTile: darkslategrey;
  font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
}

h1,
p {
  font-family: Lato;
}

#root {
  width: 100%;
  margin: 0 auto;
}
.game {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  color: black;
  border-bottom: 1px solid #3a3a3c;
}

.title {
  margin: 0 auto;
  font-weight: 700;
  font-size: 36px;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  pointer-events: none;
}

.key {
  padding: 0;
  font-family: inherit;
  font-weight: bold;
  /* margin: 0 3px 0 0; */
  height: 40px;
  width: 18%;
  border-radius: 4px;
  border-width: 1px;
  border-color: #d3d6da;
  cursor: pointer;
  user-select: none;
  background-color: #d3d6da;
  color: #1a1a1b;
  /* border-color: red; */
  margin-left: 2px;
  /*flex: 1;
    display: flex;*/
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3);
}

.keyLast {
  width: 60px;
}

.key[keyState='green'] {
  background-color: var(--darkendGreen);
  color: white;
}

.key[keyState='gray'] {
  background-color: var(--gray);
  color: white;
}

.key[keyState='yello'] {
  background-color: var(--darkendYellow);
  color: white;
}

.key[keyState='green-partial'] {
  background-color: var(--darkendGreen);
  color: white;
  border: none;
  background-image: -webkit-linear-gradient(
    -45deg,
    var(--darkendGreen) 50%,
    var(--partialTile) 50%
  );
}

.key[keyState='yello-partial'] {
  background-color: var(--darkendYellow);
  color: white;
  border: none;
  background-image: -webkit-linear-gradient(
    -45deg,
    var(--darkendYellow) 50%,
    var(--partialTile) 50%
  );
}

div {
  max-width: 600px;
  /* padding: 2px; */
  margin-top: 2px;
}

.enterKey {
  margin-bottom: 2px;
  width: 100px;
  height: 40px;
}

.keyboard[won='true'] {
  pointer-events: none;
}

.keyboardRow {
  display: flex;
  justify-content: center;
}

.keyboardLastRow {
  display: flex;
  justify-content: space-evenly;
}

.tile[data-state='empty'] {
  border: 2px solid var(--color-tone-4);
}

.tile[value='empty'] {
  border: 2px solid lightgray;
}

.tile[value='ணை'] {
  font-size: 1.2rem;
}
.tile[value='ணொ'] {
  font-size: 1.2rem;
}
.tile[value='ணோ'] {
  font-size: 1.2rem;
}
.tile[value='ணௌ'] {
  font-size: 1.2rem;
}

.tile[value]:not([value='empty']) {
  animation: zoom-in-zoom-out 0.3s;
}

.tile[color='green'] {
  background-color: var(--darkendGreen);
  color: white;
  border: none;
}

.tile[color='gray'] {
  background-color: var(--darkGray);
  color: white;
  border: none;
  animation: shake 0.5s;
}

.tile[color='yello'] {
  background-color: var(--darkendYellow);
  color: white;
  border: none;
  animation: shake 0.5s;
}

.tile[color='yello-partial'] {
  background-color: var(--darkendYellow);
  color: white;
  border: none;
  animation: shake 0.5s;
  background-image: -webkit-linear-gradient(
    -45deg,
    var(--darkendYellow) 50%,
    var(--partialTile) 50%
  );
}

.tile[color='green-partial'] {
  background-color: var(--darkendGreen);
  color: white;
  border: none;
  animation: shake 0.5s;
  background-image: -webkit-linear-gradient(
    -45deg,
    var(--darkendGreen) 50%,
    var(--partialTile) 50%
  );
}

.tile {
  display: inline-flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: bold;
  vertical-align: middle;
  box-sizing: border-box;
  color: black;
  text-transform: uppercase;
  user-select: none;
  border: 2px solid black;
}

.board {
  width: 300px;
  height: 420px;
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 5px;
  padding: 20px 10px;
  box-sizing: border-box;
  margin: auto;
}
.mainBoard[page='stats'] {
  opacity: 0.4;
}

.mainBoard[page='lost'] {
  opacity: 0.4;
}

.mainBoard[page='help'] {
  display: none;
}
.mainBoard[page='won'] {
  opacity: 0.4;
}

.mainBoard[page='prevAns'] {
  opacity: 0.4;
}

.tile-row[length='4'] {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 4px;
}

.tile-row[length='3'] {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 4px;
}

#help-button {
  box-shadow: 1px 1px;
  border: 1px;
  border-radius: 4px;
  margin-left: 10px;
  height: 30px;
}

#help-button:hover {
  transform: scale(1.2);
  color: black;
  border-color: black;
}

.dialog {
  width: 100%;
  height: 100%;
  justify-content: center;
  background-color: white;
  animation: SlideIn 100ms linear;
  z-index: 2000;
}

